.App {
  text-align: center;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: Calc(10px + 2vmin);
}
h1 {
  font-family: Lato;
}

form {
  border: 3px solid #797676;
}
.username {
  padding: 16px;
}
.Home {
  text-align: center;
}
.Wrapper {
  background-color: rgb(156, 22, 22);
  padding: 10px;
  border-radius: 10%;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
}
.InputWrapper {
  display: flex;
  justify-content: space-between;
  width: 300px;
  align-items: center;
}

.InputWrapper input {
  border-radius: 4%;
  border: 0;
  background-color: #ede7fe;
  width: 160px;
  padding: 6px;
  height: 20px;
  margin: 10px;
}
.InputWrapper button {
  width: 70px;
  height: 25px;
  border-radius: 50%;
  padding: 0;
  align-items: center;
}
