.Item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  width: 180px;
  height: 20px;
  border: 1px solid white;
  border-radius: 2px;
  padding: 10px;
}
.Item p {
  display: flex;
  margin: 0;
  font-family: "fantasy";
  font-size: 18px;
  color: whitesmoke;
}

.Item button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  padding: 0;
  outline: none;
}
